@use "../../common/colors.scss" as c;
@use "../../common/framed_elem.scss" as f;

.contact {
    @include f.frameA(c.$white-accent-1);

    >div {
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: c.$white-accent-1;

        #contactName {
            display: flex;
            flex-direction: row;
            margin-top: 3vh;
            margin-bottom: 1vh;
            background-color: transparent;

            a {
                text-align: center;
                text-decoration: none;
                $width: 50%;
                $grow: 5%;

                .tooltip-text {
                    visibility: hidden;
                    background-color: rgb(53, 53, 53);
                    color: #fff;
                    text-align: center;
                    padding: 5px 0;
                    border-radius: 6px;

                    display: block;
                    width: 120px;
                    height: 50px;
                    top: 8%;
                    left: 50%;
                    margin-left: -60px;
                    opacity: 0;

                    /* Position the tooltip text - see examples below! */
                    position: relative;
                    z-index: 1;
                    transition: opacity 100ms ease-in-out;
                    text-decoration: none;

                }

                img {
                    width: $width;
                    margin-top: 0%;
                    border-radius: 50%;
                    border: 2px solid c.$black-accent;

                    transition: all ease-in-out 0.2s;
                }

                &:hover {
                    .tooltip-text {
                        visibility: visible;
                        opacity: 1;
                    }

                    img {
                        width: $width + $grow;
                        margin-top: -$grow/2;
                        margin-bottom: -$grow/2;
                    }
                }
            }

            #pseudo {
                font-weight: bold;
                font-size: 4vh;
                margin: 0pt 0 0pt 0;
            }

            #name {
                font-size: 2.5vh;
                margin: 0;
            }

            div:nth-child(1) {
                display: flex;
                justify-content: center;
                align-items: flex-end;
                flex: 1;
                // margin-right: 1vw;
            }

            div:nth-child(2) {
                display: flex;
                justify-content: center;
                align-items: flex-start;
                flex-direction: column;
                flex: 1;
                padding-bottom: 60px;
            }
        }
    }

    .framedA-title {
        text-align: center;
    }
}